.project-description{
    -ms-flex-line-pack: center;
        align-content: center;
    margin-top: 20px;
    padding: 20px 0px 20px 10px;
    border: 10px;
    width: 100%;
    background: 
    -webkit-gradient(linear,
        left top, right top,
        from(rgba(28, 17, 10, 0.8)),
        to(rgba(42, 43, 42, 0.5))
    );
    background: 
    -webkit-linear-gradient(left,
        rgba(28, 17, 10, 0.8),
        rgba(42, 43, 42, 0.5)
    );
    background: 
    -o-linear-gradient(left,
        rgba(28, 17, 10, 0.8),
        rgba(42, 43, 42, 0.5)
    );
    background: 
    linear-gradient(to right,
        rgba(28, 17, 10, 0.8),
        rgba(42, 43, 42, 0.5)
    );
}


