@import url('https://fonts.googleapis.com/css?family=Dosis');
body {
  background:
  -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(0, 208, 255, 0.5)),
    to(rgba(255, 163, 74, 0.9))
    ),
    url(laptop.jpeg);
  background:
  -webkit-linear-gradient(
    rgba(0, 208, 255, 0.5),
    rgba(255, 163, 74, 0.9)
    ),
    url(laptop.jpeg);
  background:
  -o-linear-gradient(
    rgba(0, 208, 255, 0.5),
    rgba(255, 163, 74, 0.9)
    ),
    url(laptop.jpeg);
  background:
  linear-gradient(
    rgba(0, 208, 255, 0.5),
    rgba(255, 163, 74, 0.9)
    ),
    url(laptop.jpeg);
    background-repeat: no-repeat;
  background-size: 100%;
  background-attachment: fixed;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



